.About{
    margin: 1rem auto;
    max-width: 1440px;
    display: grid;
    grid-template-areas: 
        "photo desc"
        "photo desc";
    grid-template-columns: 600px 600px;
    align-items: center;
    justify-content: center;
}

.About__Photo__Main{
    grid-area: photo;
    max-height: 700px;
}

.About__Desc{
    grid-area: desc;
    max-width: 600px;
}

.About__Photo__Mobile{
    display: none;
}
@media (max-width: 1250px){
    .About{
        grid-template-columns: 500px 500px;
    }
}

@media (max-width: 1000px){
    .About{
        grid-template-columns: 400px 400px;
        align-items: unset;
    }
    .About__Photo__Main{
        max-height: 500px;
    }
}

@media (max-width: 850px){
    .About{
        grid-template-areas: 
        "photo"
        "desc";
        grid-template-columns: 1fr;
        margin: 1rem
    }
    .About__Photo__Main{
        display: none;
        grid-area: unset;
    }
    .About__Desc{
        max-width: auto;
    }
    .About__Photo__Mobile{
        display: unset;
        grid-area: photo;
    }
}