.Footer{
    text-align: center;
    padding-bottom: 3rem;
    display: flex;
    justify-content: space-evenly;
    margin: 0rem auto;
    /* align-content: space-between; */
    width: 900px;
}

@media (max-width: 900px){
    .Footer{
        flex-direction: column;
        width: unset;
        padding-top: 1rem;
        padding-bottom: 2rem;
    }
}